<template>
  <v-container fluid id="contacto" data-aos="zoom-out" data-aos-duration="2000">
    <v-row>
      <v-col>
        <h2 class="display-1 font-weight-bold text-center">Contacto</h2>
        <h4 class="subtitle ml-5">¿ Que Puedo hacer por ti ?</h4>
      </v-col>
    </v-row>
    <v-row>
      <v-col class="col-xs-12">
        <v-form>
          <v-text-field
            class="ml-5"
            v-model="name"
            :error-messages="nameErrors"
            label="Nombre"
            required
            @input="$v.name.$touch()"
            @blur="$v.name.$touch()"
          ></v-text-field>
          <v-text-field
            class="ml-5"
            :error-messages="emailErrors"
            @input="$v.name.$touch()"
            @blur="$v.name.$touch()"
            v-model="email"
            label="Email"
            required
          ></v-text-field>
          <v-text-field
            class="ml-5"
            v-model="telf"
            :error-messages="telfErrors"
            @input="$v.name.$touch()"
            @blur="$v.name.$touch()"
            label="Telf"
            required
            type="number"
            :counter="12"
          ></v-text-field>
          <v-textarea
            class="ml-5"
            label="Mensaje"
            required
            :error-messages="messengerErrors"
            @input="$v.name.$touch()"
            @blur="$v.name.$touch()"
            v-model="messenger"
          ></v-textarea>
          <div class="text-center">
            <v-btn class="white--text" color="#1c8b25" @click="submit">Enviar</v-btn>
          </div>
        </v-form>
      </v-col>
      <v-col class="col-4 d-none d-lg-flex flex-column" id="infoso">
        <v-row>
          <v-col>
            <h4 class="title ml-5">Social</h4>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-btn text href="https://github.com/bloodseekeers" target="_blank">
              <v-icon x-large>fab fa-github-alt</v-icon>
            </v-btn>
            <v-btn text href="https://www.instagram.com/r.s.derian/" target="_blank">
              <v-icon x-large>fab fa-instagram</v-icon>
            </v-btn>
            <v-btn
              text
              href="https://www.linkedin.com/in/derian-salazar-215b50138/"
              target="_blank"
            >
              <v-icon x-large>fab fa-linkedin-in</v-icon>
            </v-btn>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <h4 class="title ml-5">E-Mail</h4>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="ml-5">
            <v-icon x-large>fa fa-paper-plane</v-icon>
            <a class="ml-5" href="mailto:titoderian48@gmail.com">titoderian48@gmail.com</a>
          </v-col>
        </v-row>
        <h4 class="title ml-5">E-Mail</h4>
        <v-row>
          <v-col class="ml-5">
            <v-icon x-large>fas fa-phone-volume</v-icon>
            <a class="ml-5" href="tel:+674461154">+34674461154</a>
          </v-col>
        </v-row>  
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import { validationMixin } from "vuelidate";
import { required, maxLength, email } from "vuelidate/lib/validators";
export default {
  name: "ContactUs",
  mixins: [validationMixin],
  validations: {
    name: { required },
    email: { required, email },
    telf: { required, maxLength: maxLength(12) },
    messenger: { required }
  },
  data: () => ({
    name: "",
    email: "",
    telf: "",
    messenger: ""
  }),
  computed: {
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email &&
        errors.push("El correo electronico debe ser valido");
      !this.$v.email.required && errors.push("E-mail es necesario");
      return errors;
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.required && errors.push("El nombre es necesario");
      return errors;
    },
    messengerErrors() {
      const errors = [];
      if (!this.$v.messenger.$dirty) return errors;
      !this.$v.messenger.required && errors.push("El mensaje es necesario");
      return errors;
    },
    telfErrors() {
      const errors = [];
      if (!this.$v.telf.$dirty) return errors;
      !this.$v.telf.required && errors.push("El numero de telf es necesario");
      !this.$v.telf.maxLength &&
        errors.push("El numero de telf debe tener como maximo 12 numeros");
      return errors;
    }
  },
  methods: {
    submit() {
      this.$v.$touch();
    }
  }
};
</script>
