<template>
<v-container id="botonarriba" v-on="ocultarS()"  v-show="btnar">
    <v-btn
        dark
        fab
        fixed
        bottom
        left
        v-on:click = "botonupp">
    <v-icon x-large>mdi-chevron-up</v-icon>
    </v-btn>
</v-container>
</template>
<script>
export default {
    name: "BotonUp",
    data(){
        return {
            btnar: false,
    }
    },
    methods: {
        ocultarS(){
            window.onscroll = function () {        
                const botonarriba = document.querySelector('#botonarriba');     
                if (document.documentElement.scrollTop > 650) {
                    botonarriba.style.display = 'inline';
                }else {
                    botonarriba.style.display = 'none'
                }
            }
        },
        botonupp(){
            window.scrollTo({
                top:0,
                behavior: 'smooth'
            });
        },
    },
};
</script>
<style>
#botonarriba .v-btn{
    bottom: 80px;
    margin-left: 10px;
    background-color: #ac5389;
}
.dark #botonarriba .v-btn{
    background: #0f0c29;
}
</style>